/* Wordmark: ~1015% smaller, hierarchy, tighter tracking on name; responsive on small screens */
.gh-head-logo {
  font-size: clamp(1.15rem, 3.5vw + 0.8rem, 1.9rem);
  font-weight: 600;
  text-decoration: none;
  letter-spacing: 0.02em;
  transition: opacity 0.2s ease, border-color 0.2s ease;
}

.gh-head-logo:hover {
  text-decoration: none;
  opacity: 0.92;
}

.gh-head-logo:focus-visible {
  outline: 2px solid var(--mrc-surface);
  outline-offset: 3px;
}

/* "Manny Roy"  heavier, tighter tracking (reads as mark) */
.gh-head-logo .mrc-logo-serif {
  font-family: var(--font-serif);
  font-weight: 600;
  letter-spacing: -0.01em;
}

.gh-head-logo .mrc-logo-sans {
  font-family: var(--font-sans);
  font-weight: 500;
  letter-spacing: 0;
}

/* Nav: lighter than wordmark; spacing in .mrc-head overrides */
.gh-head-menu,
.gh-head-actions {
  font-size: 1.1rem;
  font-weight: 400;
}
